@use '../variables/variables.scss';

@mixin button {
  background: linear-gradient(104.34deg, variables.$second-text-color -15.34%, #BE8022 145.95%);
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  border: 0;
  padding: 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: variables.$main-color;
  margin-top: auto;
  cursor: variables.$cursor-hover;
}